.header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    padding: 0 20px;
    -ms-flex-align: center;
        align-items: center;
    text-shadow: 0px 4px 10px rgba(0,0,0,0.5);
}
.header input{
    background: transparent;
    border: none;
    height: 30px;
    margin: 5px;
    border: 1px solid white;
    color: white;
    padding: 5px;
    font-size: 1.5em;
    text-align: center;
}
.header input::-webkit-input-placeholder{
    color: white;
}
.header input::-moz-placeholder{
    color: white;
}
.header input:-ms-input-placeholder{
    color: white;
}
.header input::placeholder{
    color: white;
}
.header input:focus, .header input:active, .header input:visited{
    outline: none;
}
.header h1{
    font-size: 20px;
}
.country{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}
.country span{
    font-size: 16px;
    color: #dff1f3;
}


